import styled, { css } from "styled-components";
import { rgba } from "polished";
import vars from "../../styles/vars";
import ball from "../../assets/ball";
import { HelpWrapper } from "../../components/help/styles";
import { ButtonWrapper } from "../../components/button/styles";

export const PlayWrapper = styled.div`
  width: 100%;
  height: 100%;
  background-color: ${vars.colors.grass};
  position: relative;
  user-select: none;
`;

export const ControlsWrapper = styled.div`
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 1em;
`;

type ControlsRotateProps = {
  direction?: "left" | "right";
};

export const ControlsRotate = styled.div<ControlsRotateProps>`
  background-color: ${rgba(vars.colors.white, 0.2)};
  border-radius: 0.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5em 0;
  width: 6em;


  label,
  img {
    display: block;
    user-select: none;
    pointer-events: none;
  }

  label {
    text-align: center;
    text-transform: uppercase;
    font-size: 0.75em;
    color: ${vars.colors.white};
    margin-top: -0.5em;
  }

  img {
    width: 50%;

    ${({ direction }) => {
      if (direction === "right") {
        return css`
          transform: scaleX(-1);
        `;
      }
    }}
  }
`;

export const ControlsPower = styled.div`
  margin-left: auto;
  border-radius: 0.5em;
  background-color: ${rgba(vars.colors.white, 0.2)};
  position: relative;
  z-index: 1;
  padding: 0.75em 0.5em;

  input {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(-90deg) translateX(-100%);
    transform-origin: 0 0;
    width: 14.4em;
    height: 3em;
    opacity: 0;
  }
`;

export const ControlsPowerLine = styled.div`
  position: relative;
  height: 13.8em;

  :after {
    z-index: -1;
    content: "";
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0.3em;
    height: 13.8em;
    transform: translate(-50%, -50%);
    border: 0.2em solid #e8e8e8;
    border-radius: 2em;
    background: linear-gradient(
      #fe9404 0%,
      #f94f14 28.08%,
      #de1237 64.53%,
      #3e3496 100%
    );
  }
`;

export const ControlsPowerIndicator = styled.div`
  pointer-events: none;
  position: absolute;
  bottom: 0;
  left: 50%;
  background-color: #424242;
  border: 0.2em solid #e8e8e8;
  border-radius: 2em;
  width: 1.5em;
  height: 0.8em;
  transform: translate(-50%, 50%);
`;

export const ControlsPowerShoot = styled.div`
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1em;
  font-size: 0.6em;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: ${vars.colors.white};
  background-image: url("${ball}");
  border: 2px solid ${vars.colors.black};
`;

type ControlsGroupProps = {
  enableControls: boolean;
};

export const ControlsGroup = styled.div<ControlsGroupProps>`
  pointer-events: none;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, max-content);
  place-items: flex-end;
  place-content: center;
  grid-gap: 0.5em;

  ${HelpWrapper} {
    pointer-events: none;
  }

  ${({ enableControls }) => {
    if (enableControls) {
      return css`
        ${HelpWrapper}, ${ControlsRotate}, ${ControlsPowerLine}, ${ControlsPowerShoot} {
          pointer-events: all;
        }
      `;
    }
  }}
`;

export const InfoWrapper = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5em 0.25em;
  color: ${vars.colors.white};
  pointer-events: none;

  p {
    font-size: ${14 / vars.rootSize}em;
    text-align: center;
    margin-top: 0.25em;
  }
`;

export const InfoTitle = styled.div`
  font-size: 2em;
  color: ${vars.colors.white};
`;

const STAT_SIZE = 44 / vars.rootSize;

export const InfoStat = styled.div`
  border-radius: 50%;
  border: 2px solid ${vars.colors.white};
  background-color: ${vars.colors.primary};
  width: ${STAT_SIZE}em;
  height: ${STAT_SIZE}em;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: auto;
    height: 50%;
  }
`;

export const InfoItem = styled.div`
  background-color: ${rgba(vars.colors.white, 0.2)};
  padding: 0.5em 0.75em;
  border-radius: 1em;
`;

export const WalkThroughStep = styled.div`
  position: absolute;
  top: 100%;
  width: calc(100% - 10em);
  max-width: 12em;
  left: 50%;
  transform: translate(-50%, calc(-100% - 5em));
  pointer-events: none;

  ${ButtonWrapper} {
    pointer-events: all;
    text-align: center;
    max-width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0.25em;

    &:last-child {
      margin-bottom: 0;
    }
  }
`;

export const WalkThroughStepBubble = styled.div`
  background-color: ${vars.colors.white};
  border-radius: 2em;
  padding: 1em 1em 2.25em 1em;
  text-align: center;
`;

export const WalkThroughStepButtons = styled.div`
  transform: translateY(-1.5em);
  height: 5.5em;
`;
