import { useEffect, useMemo, useState } from "react";
import * as THREE from "three";
import { useThree } from "@react-three/fiber";
import {
  OrbitControls as Controls,
  PerspectiveCamera,
} from "@react-three/drei";
import { useGame } from "../../state/game";
import { deg2rad } from "../../utils";
import { DISTANCE, ZOOM_MAX, ZOOM_MIN } from "./consts";

let resetting = false;

export default function Camera() {
  const { setZoom, screenSize, zoom, shotInProgress, angle } = useGame();
  const { camera } = useThree();
  const fov = useMemo(() => {
    return 2 * Math.atan(screenSize.height / 2 / DISTANCE) * (180 / Math.PI);
  }, [screenSize]);

  const [controls, setControls] = useState<any>(null);

  useEffect(() => {
    if (controls === null) return;
    controls.touches.ONE = THREE.TOUCH.PAN;
    controls.touches.TWO = THREE.TOUCH.DOLLY_ROTATE;
  }, [controls]);

  useEffect(() => {
    if (!controls) return;

    function onChange(event: any) {
      if (resetting) return;
      setZoom(DISTANCE / event.target.object.position.z);
    }

    controls.addEventListener("change", onChange);
    return () => {
      controls.removeEventListener("change", onChange);
    };
  }, [controls]);

  useEffect(() => {
    if (controls) controls.enabled = false;
    camera.position.z = DISTANCE / zoom;
    if (controls) controls.enabled = true;
  }, [controls, camera, zoom]);

  useEffect(() => {
    if (controls === null) return;
    const { zoom } = useGame.getState();
    resetting = true;
    controls.reset();
    resetting = false;
    controls.zoom = zoom;
    camera.position.z = DISTANCE / zoom;
  }, [controls, angle]);

  useEffect(() => {
    if (!shotInProgress || controls === null) return;
    const { zoom } = useGame.getState();
    resetting = true;
    controls.reset();
    resetting = false;
    controls.zoom = zoom;
    camera.position.z = DISTANCE / zoom;
  }, [shotInProgress, controls, camera]);

  return (
    <>
      <PerspectiveCamera
        makeDefault
        fov={fov}
        near={DISTANCE / 100}
        far={DISTANCE * 100}
      />
      <Controls
        ref={setControls}
        enableDamping={false}
        minDistance={DISTANCE / ZOOM_MAX}
        maxDistance={DISTANCE / ZOOM_MIN}
        minAzimuthAngle={0}
        maxAzimuthAngle={0}
        minPolarAngle={deg2rad(90)}
        maxPolarAngle={0}
      />
    </>
  );
}
