import { useState } from "react";
import { HomeButtons, HomeWrapper } from "./styles";
import content from "../../content";
import Button from "../../components/button/button";
import Modal from "../../components/modal/modal";

export default function Home() {
  const [showInstructions, setShowInstructions] = useState(false);
  const [showPrizes, setShowPrizes] = useState(false);

  return (
    <HomeWrapper>
      <h1>Play to win</h1>

      <HomeButtons>
        <Button onClick={() => setShowInstructions(true)}>How to Play</Button>
        <Button onClick={() => setShowPrizes(true)}>Prizes</Button>
      </HomeButtons>

      <Button to="/start" variant="secondary">
        <span className="font-gradient">START</span>
      </Button>

      <Modal
        open={showInstructions}
        onClose={() => setShowInstructions(false)}
        title="How to Play"
      >
        {content.howToPlay}
      </Modal>
      <Modal
        open={showPrizes}
        onClose={() => setShowPrizes(false)}
        title="Prizes & Terms"
      >
        {content.prizes}
      </Modal>
    </HomeWrapper>
  );
}
