import { ChangeEvent, useCallback, useEffect, useRef, useState } from "react";
import {
  ControlsGroup,
  ControlsPower,
  ControlsPowerIndicator,
  ControlsPowerLine,
  ControlsPowerShoot,
  ControlsRotate,
  ControlsWrapper,
  WalkThroughStep,
  WalkThroughStepBubble,
  WalkThroughStepButtons,
} from "./styles";
import { BALL_START_X, BALL_START_Y } from "./consts";
import rotate from "../../assets/rotate";
import { useGame } from "../../state/game";
import Help from "../../components/help/help";
import content from "../../content";
import Modal from "../../components/modal/modal";
import Button from "../../components/button/button";

export default function Controls() {
  const [showInstructions, setShowInstructions] = useState(false);
  const {
    power,
    setPower,
    enableControls,
    walkThroughStep,
    setWalkThroughStep,
    isPractice,
    shotInProgress,
    setIsPractice,
    setBallPosition,
    setTotalShots,
  } = useGame();
  const angleDirection = useRef(0);
  const [pointerDown, setPointerDown] = useState(false);
  const practiceShotTaken = useRef(false);

  const rotateDirection = useCallback((direction: number) => {
    return () => {
      angleDirection.current = direction;
    };
  }, []);

  useEffect(() => {
    if (!pointerDown || angleDirection.current === 0) return;
    const smallIncrement = 1;
    const bigIncrement = 3;
    const { angle, setAngle } = useGame.getState();
    setAngle(angle + smallIncrement * angleDirection.current);

    const delay = 200;
    let nextFrame: number;
    let actionRate = 1000 / 30;
    let actionStartTime = Date.now();
    let lastActionTime = 0;
    (function action() {
      nextFrame = requestAnimationFrame(action);
      const now = Date.now();
      if (now - actionStartTime < delay) return;
      if (now - lastActionTime >= actionRate) {
        lastActionTime = now;
        const { angle, setAngle } = useGame.getState();
        setAngle(angle + bigIncrement * angleDirection.current);
      }
    })();
    return () => {
      cancelAnimationFrame(nextFrame);
    };
  }, [pointerDown]);

  useEffect(() => {
    function onPointerDown() {
      setPointerDown(true);
    }

    function onPointerUp() {
      angleDirection.current = 0;
      setPointerDown(false);
    }

    window.addEventListener("pointerdown", onPointerDown);
    window.addEventListener("pointerup", onPointerUp);
    return () => {
      window.removeEventListener("pointerdown", onPointerDown);
      window.removeEventListener("pointerup", onPointerUp);
    };
  }, []);

  const onPowerChange = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    setPower(parseFloat(event.target.value));
  }, []);

  const shoot = useCallback(() => {
    const { setShotInProgress, setShotStartTime, setEnableControls } =
      useGame.getState();
    setShotStartTime(Date.now());
    setEnableControls(false);
    setShotInProgress(true);
    if (isPractice) {
      practiceShotTaken.current = true;
    }
    setWalkThroughStep("");
  }, [isPractice]);

  const help = useCallback(() => {
    setShowInstructions(true);
  }, []);

  useEffect(() => {
    if (isPractice && practiceShotTaken.current && !shotInProgress) {
      const timeout = setTimeout(() => {
        setBallPosition([BALL_START_X, BALL_START_Y]);
        setTotalShots(0);
        setIsPractice(false);
        practiceShotTaken.current = false;
        useGame.getState().setWalkThroughStep("practice-taken");
      }, 500);
      return () => clearTimeout(timeout);
    }
  }, [isPractice, shotInProgress]);

  return (
    <>
      {walkThroughStep !== "" && (
        <WalkThroughStep>
          <WalkThroughStepBubble>
            {walkThroughStep === "practice" && (
              <>
                Do you want to take <br />a practice shot first?
              </>
            )}
            {walkThroughStep === "rotate" && (
              <>
                Aim your shot <br />
                by pressing the <br />
                rotate buttons below
              </>
            )}
            {walkThroughStep === "zoom" && (
              <>
                Pinch to zoom <br />
                in and out
              </>
            )}
            {walkThroughStep === "power" && (
              <>
                Set the power limit <br />
                by pulling the bar <br />
                up or down
              </>
            )}
            {walkThroughStep === "shoot" && (
              <>
                Press the ‘SHOOT’ <br />
                button
              </>
            )}
            {walkThroughStep === "practice-taken" && <>Play for real?</>}
          </WalkThroughStepBubble>
          <WalkThroughStepButtons>
            {walkThroughStep === "practice" && (
              <>
                <Button
                  onClick={() => {
                    setIsPractice(true);
                    setWalkThroughStep("rotate");
                  }}
                >
                  Yes
                </Button>
                <Button variant="black" onClick={() => setWalkThroughStep("")}>
                  No
                </Button>
              </>
            )}
            {walkThroughStep === "rotate" && (
              <>
                <Button onClick={() => setWalkThroughStep("zoom")}>Next</Button>
              </>
            )}
            {walkThroughStep === "zoom" && (
              <>
                <Button onClick={() => setWalkThroughStep("power")}>
                  Next
                </Button>
              </>
            )}
            {walkThroughStep === "power" && (
              <>
                <Button onClick={() => setWalkThroughStep("shoot")}>
                  Next
                </Button>
              </>
            )}
            {walkThroughStep === "shoot" && (
              <>
                <Button onClick={() => setWalkThroughStep("")}>Close</Button>
              </>
            )}
            {walkThroughStep === "practice-taken" && (
              <>
                <Button onClick={() => setWalkThroughStep("")}>Yes</Button>
              </>
            )}
          </WalkThroughStepButtons>
        </WalkThroughStep>
      )}
      <ControlsWrapper>
        <ControlsGroup enableControls={enableControls}>
          <Help onClick={help} />
          <ControlsRotate onPointerDown={rotateDirection(1)}>
            <img src={rotate} alt="" />
            <label>Rotate Left</label>
          </ControlsRotate>
          <ControlsRotate onPointerDown={rotateDirection(-1)} direction="right">
            <img src={rotate} alt="" />
            <label>Rotate Right</label>
          </ControlsRotate>
          <ControlsPower>
            <ControlsPowerLine>
              <input
                type="range"
                min={0}
                max={100}
                step={0.01}
                onChange={onPowerChange}
                value={power}
              />
              <ControlsPowerIndicator style={{ bottom: `${power}%` }} />
            </ControlsPowerLine>
            <ControlsPowerShoot onClick={shoot}>Shoot</ControlsPowerShoot>
          </ControlsPower>
        </ControlsGroup>
      </ControlsWrapper>
      <Modal
        open={showInstructions}
        onClose={() => setShowInstructions(false)}
        title="How to Play"
      >
        {content.howToPlay}
      </Modal>
    </>
  );
}
