import React from "react";
import { InfoItem, InfoStat, InfoTitle, InfoWrapper } from "./styles";
import { useGame } from "../../state/game";
import windArrow from "../../assets/wind-arrow";

export default function Info() {
  const { totalShots, windDirection, windSpeed } = useGame();
  return (
    <InfoWrapper>
      <InfoItem>
        <InfoStat>{totalShots}</InfoStat>
        <p>Par 5</p>
      </InfoItem>
      <InfoTitle>Hole 18</InfoTitle>
      <InfoItem>
        <InfoStat>
          <img
            src={windArrow}
            alt=""
            style={{ transform: `rotate(${windDirection}deg)` }}
          />
        </InfoStat>
        <p>{windSpeed}mph</p>
      </InfoItem>
    </InfoWrapper>
  );
}
