import React, { useMemo, useRef } from "react";
import * as THREE from "three";
import { useFrame } from "@react-three/fiber";
import { CubicBezierLine, useTexture } from "@react-three/drei";
import { useGame } from "../../state/game";
import ball from "../../assets/ball";
import { map } from "../../utils";
import { MAX_DISTANCE, MIN_SHOT_POWER, ORIGINAL_MAP_HEIGHT } from "./consts";

const uniforms = {
  iResolution: {
    value: new THREE.Vector2(1 / 50, 1),
  },
  iMouse: {
    value: new THREE.Vector3(0.5, 0.5, 0.5),
  },
  iTime: {
    value: 0,
  },
  uDistance: {
    value: 1,
  },
  uWindDirection: {
    value: 1,
  },
  uWindSpeed: {
    value: 1,
  },
};

export default function Ball() {
  const {
    ballSize,
    power,
    screenSize,
    shotInProgress,
    inHole,
    windDirection,
    windSpeed,
  } = useGame();
  const [dashOffset, setDashOffset] = React.useState(0);
  const [texture] = useTexture([ball]);
  const distance = useMemo(() => {
    const scaledPower = map(power, 0, 100, MIN_SHOT_POWER, 100) / 100;
    const maxDistance = ORIGINAL_MAP_HEIGHT * MAX_DISTANCE;
    const scale = screenSize.height / ORIGINAL_MAP_HEIGHT;
    return maxDistance * scaledPower * scale;
  }, [power, screenSize, ballSize]);

  useMemo(() => {
    const imgRatio = texture.image.width / texture.image.height;
    const planeRatio = 1;
    const ratio = planeRatio / imgRatio;
    texture.repeat.x = ratio;
    texture.offset.x = 0.5 * (1 - ratio);
  }, [texture]);

  const ballRef = useRef<THREE.Mesh>();
  useFrame(({ clock }) => {
    uniforms.iTime.value = clock.getElapsedTime();
    uniforms.iResolution.value.x = 1 / 50;
    uniforms.iResolution.value.y = 1 / distance;
    uniforms.uDistance.value = distance;
    uniforms.uWindDirection.value = windDirection;
    uniforms.uWindSpeed.value = windSpeed;
    setDashOffset((dashOffset) => {
      return dashOffset === -1 ? 0 : dashOffset - 0.1;
    });
    if (inHole && ballRef.current) {
      if (ballRef.current.material instanceof THREE.MeshStandardMaterial) {
        ballRef.current.material.opacity = Math.max(
          0,
          ballRef.current.material.opacity - 0.05
        );
      }
    }
  });

  const { start, midA, midB, end } = useMemo(() => {
    const d = distance;
    const scaledPower = map(power, 0, 100, MIN_SHOT_POWER, 100) / 100;
    const offsetX = 1;
    // @note removed by client request
    // const offsetX = map(
    //   windDirection,
    //   WIND_DIRECTION_MIN,
    //   WIND_DIRECTION_MAX,
    //   windSpeed,
    //   -windSpeed
    // ) * SHOT_DIVERSION_FACTOR;
    const start = [0, -d, 0];
    const midA = [offsetX * 0.5 * scaledPower, -d * 0.5, 0];
    const midB = [offsetX * 0.5 * scaledPower, -d * 0.5, 0];
    const end = [-offsetX * scaledPower, 0, 0];
    return {
      start: new THREE.Vector3(...start),
      midA: new THREE.Vector3(...midA),
      midB: new THREE.Vector3(...midB),
      end: new THREE.Vector3(...end),
    };
  }, [power, distance, windSpeed, windDirection]);

  return (
    <group>
      <mesh ref={ballRef}>
        <circleBufferGeometry attach="geometry" args={[ballSize, 100]} />
        <meshStandardMaterial attach="material" map={texture} transparent />
      </mesh>
      <group position={[0, distance, -0.01]}>
        <CubicBezierLine
          visible={!shotInProgress && !inHole}
          start={start}
          midA={midA}
          midB={midB}
          end={end}
          lineWidth={ballSize * Math.PI}
          alphaToCoverage={true}
          color="#ffffff"
          dashed={true}
          dashOffset={dashOffset}
        />
      </group>
    </group>
  );
}
