import { useEffect } from "react";
import { useGame } from "../state/game";
import { clamp, easeOut, map } from "../utils";
import {
  BALL_SIZE,
  BALL_SIZE_MAX,
  MAX_DISTANCE,
  MIN_SHOT_POWER,
  ORIGINAL_MAP_HEIGHT,
  SHOT_DIVERSION_FACTOR,
  WIND_DIRECTION_MAX,
  WIND_DIRECTION_MIN,
} from "../screens/play/consts";
import assets from "../config/assets";
import CustomEase from "../services/custom-ease/custom-ease";

const ease = CustomEase.create(
  "custom",
  // "M0,0 C0.255,0.501 0.611,0.698 0.702,0.826 0.712,0.841 0.734,0.858 0.745,0.86 0.759,0.862 0.752,0.832 0.766,0.832 0.784,0.832 0.794,0.871 0.802,0.858 0.888,0.832 1,1 1,1 "
  "M0,0 C0.255,0.501 0.588,0.7 0.668,0.826 0.678,0.841 0.7,0.872 0.711,0.874 0.725,0.876 0.76,0.832 0.788,0.832 0.828,0.832 0.854,0.901 0.862,0.888 0.948,0.862 1,1 1,1 "
);

const MIN_DURATION = 1000;
const MAX_DURATION = 7000;
const APOGEE = 0.5;
const PERIGEE = 0.87;

const audioTeeOff = new Audio(assets.audio.teeoff);
const audioPutt = new Audio(assets.audio.putt);
const audioHole = new Audio(assets.audio.hole);

export default function useShoot() {
  const { shotInProgress, inHole } = useGame();

  useEffect(() => {
    if (inHole) {
      audioHole.currentTime = 0;
      audioHole.play();
      return;
    }

    if (!shotInProgress) return;

    const { ballPosition, setBallStartPosition } = useGame.getState();
    setBallStartPosition([...ballPosition]);

    const { power } = useGame.getState();
    const scaledPower = map(power, 0, 100, MIN_SHOT_POWER, 100) / 100;
    const maxDistance = ORIGINAL_MAP_HEIGHT * MAX_DISTANCE;
    const distance = maxDistance * scaledPower;
    const duration = map(scaledPower, 0, 1, MIN_DURATION, MAX_DURATION);
    const ballMaxSize = map(distance, 0, maxDistance, 1, BALL_SIZE_MAX);
    const puttingPower = 0.35;

    if (scaledPower < puttingPower) {
      audioPutt.currentTime = 0;
      audioPutt.play();
    } else {
      audioTeeOff.currentTime = 0;
      audioTeeOff.play();
    }

    let nextFrame = requestAnimationFrame(animate);

    let ballSize = BALL_SIZE;

    function animate() {
      nextFrame = requestAnimationFrame(animate);
      const {
        shotStartTime,
        ballStartPosition,
        setBallPosition,
        setEnableControls,
        setBallSize,
        setShotInProgress,
        windDirection,
        windSpeed,
        screenSize,
      } = useGame.getState();
      const now = Date.now();
      const startX = ballStartPosition[0];
      const startY = ballStartPosition[1];

      const t = clamp(
        map(now, shotStartTime, shotStartTime + duration, 0, 1),
        0,
        1
      );
      let progress;
      if (scaledPower < puttingPower) {
        progress = easeOut(t);
      } else {
        progress = ease(t);
      }

      if (progress <= APOGEE) {
        ballSize = map(progress, 0, APOGEE, BALL_SIZE, ballMaxSize);
      }

      if (progress > APOGEE) {
        ballSize = map(progress, APOGEE, PERIGEE, ballMaxSize, BALL_SIZE);
      }

      ballSize = clamp(ballSize, BALL_SIZE, ballMaxSize);

      if (scaledPower < puttingPower) {
        ballSize = BALL_SIZE;
      }

      const scale = ORIGINAL_MAP_HEIGHT / screenSize.height;
      const offsetX =
        map(
          windDirection,
          WIND_DIRECTION_MIN,
          WIND_DIRECTION_MAX,
          windSpeed,
          -windSpeed
        ) * SHOT_DIVERSION_FACTOR;
      const scaleOffset = map(distance, 0, maxDistance, 0, 1);

      const newX = map(
        progress,
        0,
        1,
        startX,
        startX - offsetX * scale * scaleOffset
      );
      const newY = map(progress, 0, 1, startY, startY - distance);

      setBallSize(ballSize);
      setBallPosition([newX, newY]);

      if (t === 1) {
        setShotInProgress(false);
        setEnableControls(true);
        cancelAnimationFrame(nextFrame);
      }
    }

    return () => cancelAnimationFrame(nextFrame);
  }, [shotInProgress, inHole]);
}
