import { createGlobalStyle } from "styled-components";
import { fluidRange } from "polished";
import vars from "./vars";

export default createGlobalStyle`
  @font-face {
    font-family: "Sky Text";
    src: url("data:font/woff2;charset=utf-8;base64,")
    format("woff2");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "Sky Text";
    src: url("data:font/woff2;charset=utf-8;base64,")
    format("woff2");
    font-weight: 500;
    font-style: normal;
    font-display: swap;
  }

  * {
    box-sizing: border-box;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    line-height: ${vars.lineHeight};
    color: inherit;
    padding: 0;
    margin: 0;
  }

  html {
    font-family: ${vars.fontFamily};
    ${fluidRange(
      {
        prop: "font-size",
        fromSize: `${vars.rootSize * 0.85}px`,
        toSize: `${vars.rootSize}px`,
      },
      `${vars.responsiveMin}px`,
      `${vars.responsiveMax}px`
    )};
    font-weight: normal;
    position: fixed;
    overflow: hidden;
    background-color: ${vars.colors.black};
  }

  html, body, #root {
    width: 100%;
    height: 100%;
  }

  img, svg {
    width: 100%;
    max-width: 100%;
  }

  ul,
  ol {
    padding-left: 1.2em;
    margin-bottom: 1em;

    :last-child {
      margin-bottom: 0;
    }
  }

  li,
  p {
    margin-bottom: 1em;

    :last-child {
      margin-bottom: 0;
    }
  }

  #root {
    width: 100%;
    max-width: ${vars.responsiveMax}px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    position: relative;
    background-color: ${vars.colors.white};
  }

  .font-gradient {
    background: linear-gradient(
      90deg,
      #fe9504 0%,
      #fe650e 38.42%,
      #ec1425 61.58%,
      #af0870 81.28%,
      #393697 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
`;
