import { useEffect, useState } from "react";
import { ResultsWrapper } from "./styles";
import { StartStar } from "../start/styles";
import star from "../../assets/star.png";
import Button from "../../components/button/button";
import { useGame } from "../../state/game";

export default function Results() {
  const { totalShots } = useGame();
  const [result, setResult] = useState("Over Par");

  useEffect(() => {
    (document as any).GAME.emit("result", { score: totalShots });
    if (totalShots === 1) {
      return setResult("a Hole in One");
    }
    if (totalShots === 2) {
      return setResult("a Double Eagle");
    }
    if (totalShots === 3) {
      return setResult("an Eagle");
    }
    if (totalShots === 4) {
      return setResult("a Birdie");
    }
    if (totalShots === 5) {
      return setResult("Par");
    }
    if (totalShots === 6) {
      return setResult("a Bogey");
    }
    if (totalShots === 7) {
      return setResult("a Double bogey");
    }
    if (totalShots > 7) {
      return setResult("Over Par");
    }
  }, [totalShots]);

  return (
    <ResultsWrapper>
      <h1>
        {totalShots <= 3
          ? "Amazing!"
          : totalShots === 4
          ? "Well done!"
          : totalShots === 5
          ? "Good job"
          : null}
        <br />
        You scored {result}
      </h1>

      <StartStar src={star} alt="" />

      <p>
        {totalShots <= 5
          ? "You can now enter our draw to win a home entertainment bundle and a £20/€24 Uber Eats voucher."
          : "You can now enter our draw to win a £20/€24 Uber Eats voucher."}
      </p>

      <Button variant="secondary" onClick={() => console.log("continue")}>
        Continue
      </Button>
    </ResultsWrapper>
  );
}
