import {
  ModalContent,
  ModalContentScroll,
  ModalTitle,
  ModalWrapper,
} from "./styles";
import Button from "../button/button";

type Props = {
  children: any;
  open: boolean;
  onClose?: Function;
  title?: any;
};

export default function Modal({ children, open, onClose, title }: Props) {
  return (
    <ModalWrapper open={open}>
      <ModalContent>
        {title && <ModalTitle>{title}</ModalTitle>}
        <ModalContentScroll>{children}</ModalContentScroll>
        <Button onClick={onClose}>CLOSE</Button>
      </ModalContent>
    </ModalWrapper>
  );
}
