import { ORIGINAL_MAP_HEIGHT, ORIGINAL_MAP_WIDTH } from "./screens/play/consts";
import { useGame } from "./state/game";

export function deg2rad(deg: number): number {
  return deg * (Math.PI / 180);
}

export function map(
  value: number,
  x1: number,
  y1: number,
  x2: number,
  y2: number
): number {
  return ((value - x1) * (y2 - x2)) / (y1 - x1) + x2;
}

export function clamp(value: number, min: number, max: number): number {
  return Math.min(Math.max(value, min), max);
}

export function easeOut(t: number): number {
  return t * (2 - t);
}

export function encodeSvg(svg: string): string {
  return `data:image/svg+xml;utf8,${encodeURIComponent(svg)}`;
}

export function mapToWorldCoords(
  x: number,
  y: number
): { x: number; y: number } {
  return {
    x: map(
      x,
      0,
      ORIGINAL_MAP_WIDTH,
      ORIGINAL_MAP_WIDTH / 2,
      -ORIGINAL_MAP_WIDTH / 2
    ),
    y: map(
      y,
      0,
      ORIGINAL_MAP_HEIGHT,
      -ORIGINAL_MAP_HEIGHT / 2,
      ORIGINAL_MAP_HEIGHT / 2
    ),
  };
}

export function getPosition() {
  const { ballPosition } = useGame.getState();
  const [x, y] = ballPosition;
  return mapToWorldCoords(x, y);
}

export function rand(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1) + min);
}
