import { useEffect, useMemo, useRef } from "react";
import { useHistory } from "react-router-dom";
import * as THREE from "three";
import { SVGLoader } from "three/examples/jsm/loaders/SVGLoader";
import { useLoader, useThree } from "@react-three/fiber";
import { useTexture } from "@react-three/drei";
import { mapBoundsSvg } from "../../assets/map";
import { DEBUG_GAME, ORIGINAL_MAP_HEIGHT, ORIGINAL_MAP_WIDTH } from "./consts";
import { useGame } from "../../state/game";
import { deg2rad, getPosition, mapToWorldCoords } from "../../utils";
import assets from "../../config/assets";

let lastAngle = 0;
let lastX = 0;
let lastY = 0;

const HOLE_POSITION = mapToWorldCoords(708.5, 663);
const HOLE_SIZE = 8;
const TEXTURE_SCALE = 1.5;

export default function Map() {
  const history = useHistory();
  const {
    ballStartPosition,
    ballPosition,
    angle,
    screenSize,
    shotInProgress,
    setInHole,
  } = useGame();
  const mesh = useRef<THREE.Mesh>();
  const bounds = useRef<THREE.Mesh>();
  const hole = useRef<THREE.Mesh>();
  const svg = useLoader(SVGLoader, mapBoundsSvg);
  const [texture] = useTexture([assets.images.map]);

  useEffect(() => {
    const { x, y } = getPosition();
    const width = ORIGINAL_MAP_WIDTH;
    const height = ORIGINAL_MAP_HEIGHT;
    bounds.current.geometry.center();
    bounds.current.geometry.translate(
      x + width * 0.085,
      -y + height * 0.015,
      0
    );
    bounds.current.geometry.rotateZ(deg2rad(angle));
    mesh.current.geometry.center();
    mesh.current.geometry.translate(x, y, 0);
    mesh.current.geometry.rotateZ(deg2rad(-angle));
    hole.current.geometry.center();
    hole.current.geometry.translate(
      x - HOLE_POSITION.x,
      y - HOLE_POSITION.y,
      0
    );
    if (lastX !== x) lastX = x;
    if (lastY !== y) lastY = y;
    if (lastAngle !== angle) lastAngle = angle;
  }, [screenSize]);

  useEffect(() => {
    bounds.current.geometry.rotateZ(deg2rad(angle - lastAngle));
    mesh.current.geometry.rotateZ(deg2rad(lastAngle - angle));
    hole.current.geometry.rotateZ(deg2rad(lastAngle - angle));
    if (lastAngle !== angle) lastAngle = angle;
  }, [angle]);

  useEffect(() => {
    const { x, y } = getPosition();
    bounds.current.geometry.translate(x - lastX, lastY - y, 0);
    mesh.current.geometry.translate(x - lastX, y - lastY, 0);
    hole.current.geometry.translate(x - lastX, y - lastY, 0);
    if (lastX !== x) lastX = x;
    if (lastY !== y) lastY = y;
  }, [ballPosition]);

  const { raycaster, camera } = useThree();

  // Shot count increment
  useEffect(() => {
    const ballChange =
      ballPosition[0] -
        ballStartPosition[0] +
        (ballPosition[1] - ballStartPosition[1]) !==
      0;
    if (shotInProgress || !ballChange) {
      return;
    }
    const { totalShots, setTotalShots } = useGame.getState();
    setTotalShots(totalShots + 1);
  }, [ballStartPosition, ballPosition, shotInProgress]);

  // Out of bounds detection
  useEffect(() => {
    const ballChange =
      ballPosition[0] -
        ballStartPosition[0] +
        (ballPosition[1] - ballStartPosition[1]) !==
      0;
    if (shotInProgress || !ballChange) {
      return;
    }
    raycaster.setFromCamera({ x: 0, y: 0 }, camera);
    const intersections = raycaster.intersectObjects([bounds.current]);
    const inBounds = intersections?.[0]?.object === bounds.current;
    const { setBallPosition } = useGame.getState();
    if (!inBounds) {
      setBallPosition([...ballStartPosition]);
    }
  }, [ballStartPosition, ballPosition, raycaster, camera, shotInProgress]);

  // Hole detection
  useEffect(() => {
    const ballChange =
      ballPosition[0] -
        ballStartPosition[0] +
        (ballPosition[1] - ballStartPosition[1]) !==
      0;
    if (shotInProgress || !ballChange) {
      return;
    }
    raycaster.setFromCamera({ x: 0, y: 0 }, camera);
    const intersections = raycaster.intersectObjects([hole.current]);
    const inHole = intersections?.[0]?.object === hole.current;
    setInHole(inHole);
  }, [
    ballStartPosition,
    ballPosition,
    raycaster,
    camera,
    shotInProgress,
    history,
  ]);

  const shapes = useMemo(() => {
    return SVGLoader.createShapes(svg.paths[0]);
  }, [screenSize, svg]);

  const scale = useMemo(() => {
    return screenSize.height / ORIGINAL_MAP_HEIGHT;
  }, [screenSize]);

  return (
    <group position={[0, 0, -0.01]}>
      <group scale={[scale, scale, scale]}>
        <mesh ref={mesh}>
          <planeBufferGeometry
            attach="geometry"
            args={[ORIGINAL_MAP_WIDTH, ORIGINAL_MAP_HEIGHT, 10, 10]}
          />
          <meshStandardMaterial attach="material" map={texture} />
        </mesh>
        <mesh
          ref={bounds}
          scale={[1, -1, 1]}
          position={[0, 0, 0.01]}
          visible={DEBUG_GAME}
        >
          <shapeBufferGeometry attach="geometry" args={[shapes]} />
          <meshBasicMaterial
            attach="material"
            color="#ff0000"
            side={THREE.DoubleSide}
            transparent
            opacity={0.5}
          />
        </mesh>
        <mesh ref={hole} position={[0, 0, 0.02]} visible={DEBUG_GAME}>
          <circleBufferGeometry
            attach="geometry"
            args={[HOLE_SIZE, HOLE_SIZE * 2]}
          />
          <meshBasicMaterial
            attach="material"
            color="#000000"
            side={THREE.DoubleSide}
            transparent
            opacity={0.7}
          />
        </mesh>
      </group>
    </group>
  );
}
