import styled from "styled-components";
import { rgba } from "polished";
import vars from "../../styles/vars";
import { ButtonWrapper } from "../button/styles";
import { scroll } from "../../styles/mixins";

type ModalWrapperProps = {
  open: boolean;
};

const TRANSITION_TIME = 0.5; // in seconds

export const ModalContent = styled.div`
  background-color: ${vars.colors.white};
  border-radius: 2em;
  padding: 1em;
  max-height: calc(100% - 1.5em);
  display: flex;
  flex-direction: column;

  ${ButtonWrapper} {
    margin-bottom: -1em;
    transform: translateY(50%);
    max-width: min-content;
    margin-left: auto;
    margin-right: auto;
  }
`;

export const ModalTitle = styled.div`
  text-transform: uppercase;
  margin-bottom: 1.5em;
`;

export const ModalContentScroll = styled.div`
  ${scroll};

  strong {
    font-weight: bold;
  }

  ol.terms-cond {
    padding-left: 1.5em;
  }

  ol > li > ol > li{
    margin-bottom: 0.2em;

    &:first-child {
      margin-top: 1em;
    }
  }

`;

export const ModalWrapper = styled.div<ModalWrapperProps>`
  position: absolute;
  z-index: ${vars.zIndex.modal};
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: ${({ open }) => {
    return rgba(vars.colors.black, open ? 0.8 : 0);
  }};
  pointer-events: ${({ open }) => (open ? "all" : "none")};
  padding: 1em;
  transition: background-color ${TRANSITION_TIME * 0.5}s ease-in-out;

  ${ModalContent} {
    transition: transform ${TRANSITION_TIME}s ease-in-out;
    transform: translateY(${({ open }) => (open ? 0 : 100)}vh);
  }
`;
