import create from "zustand";
import {
  BALL_SIZE,
  BALL_START_X,
  BALL_START_Y,
  WIND_DIRECTION_MAX,
  WIND_DIRECTION_MIN,
  WIND_SPEED_MAX,
  WIND_SPEED_MIN,
  ZOOM_MAX,
} from "../screens/play/consts";
import { rand } from "../utils";

export type Vector2D = [x: number, y: number];

type Dimension = {
  width: number;
  height: number;
};

export type GameState = {
  zoom: number;
  setZoom: (zoom: number) => void;
  ballSize: number;
  setBallSize: (ballSize: number) => void;
  ballPosition: Vector2D;
  setBallPosition: (ballPosition: Vector2D) => void;
  ballStartPosition: Vector2D;
  setBallStartPosition: (ballStartPosition: Vector2D) => void;
  angle: number;
  setAngle: (angle: number) => void;
  screenSize: Dimension;
  setScreenSize: (screenSize: Dimension) => void;
  power: number;
  setPower: (power: number) => void;
  enableControls: boolean;
  setEnableControls: (enableControls: boolean) => void;
  shotInProgress: boolean;
  setShotInProgress: (shotInProgress: boolean) => void;
  shotStartTime: number;
  setShotStartTime: (shotStartTime: number) => void;
  windSpeed: number;
  setWindSpeed: (windSpeed: number) => void;
  windDirection: number;
  setWindDirection: (windDirection: number) => void;
  totalShots: number;
  setTotalShots: (totalShots: number) => void;
  inHole: boolean;
  setInHole: (inHole: boolean) => void;
  isPractice: boolean;
  setIsPractice: (isPractice: boolean) => void;
  walkThroughStep: string;
  setWalkThroughStep: (walkThroughStep: string) => void;
};

export const useGame = create<GameState>((set) => {
  return {
    zoom: ZOOM_MAX * 0.5,
    setZoom(zoom: number) {
      set({ zoom });
    },
    ballSize: BALL_SIZE,
    setBallSize(ballSize: number) {
      set({ ballSize });
    },
    ballPosition: [BALL_START_X, BALL_START_Y],
    setBallPosition(ballPosition: Vector2D) {
      set({ ballPosition });
    },
    ballStartPosition: [BALL_START_X, BALL_START_Y],
    setBallStartPosition(ballStartPosition: Vector2D) {
      set({ ballStartPosition });
    },
    angle: 0,
    setAngle(angle: number) {
      set({ angle });
    },
    screenSize: {
      width: window.innerWidth,
      height: window.innerHeight,
    },
    setScreenSize(screenSize: Dimension) {
      set({ screenSize });
    },
    power: 0,
    setPower(power: number) {
      set({ power });
    },
    enableControls: true,
    setEnableControls(enableControls: boolean) {
      set({ enableControls });
    },
    shotInProgress: false,
    setShotInProgress(shotInProgress: boolean) {
      set({ shotInProgress });
    },
    shotStartTime: 0,
    setShotStartTime(shotStartTime: number) {
      set({ shotStartTime });
    },
    windSpeed: rand(WIND_SPEED_MIN, WIND_SPEED_MAX),
    // windSpeed: WIND_SPEED_MAX,
    setWindSpeed(windSpeed: number) {
      set({ windSpeed });
    },
    windDirection: rand(WIND_DIRECTION_MIN, WIND_DIRECTION_MAX),
    // windDirection: WIND_DIRECTION_MAX,
    setWindDirection(windDirection: number) {
      set({ windDirection });
    },
    totalShots: parseInt(localStorage.getItem("sky-game-totalShots")) ?? 0,
    setTotalShots(totalShots: number) {
      localStorage.setItem("sky-game-totalShots", JSON.stringify(totalShots))
      set({ totalShots });
    },
    inHole: false,
    setInHole(inHole: boolean) {
      set({ inHole });
    },
    isPractice: false,
    setIsPractice(isPractice: boolean) {
      set({ isPractice });
    },
    walkThroughStep: "practice",
    setWalkThroughStep(walkThroughStep: string) {
      set({ walkThroughStep });
    },
  };
});

window.addEventListener("resize", () => {
  useGame.getState().setScreenSize({
    width: window.innerWidth,
    height: window.innerHeight,
  });
});
